import { useEffect, useState, useRef } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';
import { CarouselCard } from '../carouselCard';
import { SERVERURL } from '../App';

export default function Edit() {
    const params = useParams();
    const navigate = useNavigate();

    const [kode, setKode] = useState(params.id);
    const [data, setData] = useState({
        _id: "",
        kode: "",
        warna: "",
        harga: "",
        favorit: "",
        kategori: "",
        urlArray: []
    });
    const [gownId, setGownId] = useState('');
    const [urlArrayId, setUrlArrayID] = useState([]);
    const [deleteConfirm, setDeleteConfirm] = useState(false);
    const [favorit, setFavorit] = useState(false);

    const [loadingState, setLoadingState] = useState(true);

    const formRef = useRef();

    useEffect(() => {
        const controller = new AbortController();
        const signal = controller.signal;

        const url = `${SERVERURL}api/v1/gown/kode/${kode}`;
        console.log(url)
        axios.get(url, { signal })
            .then(res => {
                setData(res.data.gownFound[0]);
                setUrlArrayID(res.data.gownFound[0].urlArray);
                setGownId(res.data.gownFound[0]._id);
                setFavorit(res.data.gownFound[0].favorit);
            })
            .catch((error) => {
                if (axios.isCancel(error)) {
                    console.log("Cancelled");
                } else if (error.message === 'Network Error') {
                    alert(
                        'Cant connect to server.\n' +
                        'Please try again later and if problem persist, please contact us through Whatsapp.\n' +
                        '(Link on the bottom of the page)'
                    );
                } else console.log(error);
            }).finally(() => setTimeout(() => { setLoadingState(false) }, 2500))

        return () => controller.abort();
    }, [])

    const Delete = async () => {
        console.log('delete')
        const data = { id: gownId, kode: kode };
        await fetch(SERVERURL + 'admin/delete', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data),
        })
            .then(res => {
                if (res.status === 200) {
                    console.log('Success');
                    alert('Deleted');
                    navigate('/');
                } else if (res.status === 404) {
                    console.log('Cant Find data');
                    alert('Cant Find data');
                    navigate('/');
                } else {
                    console.log("Status: " + res.status)
                    return Promise.reject("server")
                }
            })
            .catch(err => {
                console.log(err);
                alert('Failed to Delete...');
            })
    }

    const Submit = async (e) => {
        setLoadingState(true);
        e.preventDefault();
        await fetch(SERVERURL + 'admin/edit', {
            method: 'POST',
            body: new FormData(formRef.current),
        })
            .then(res => {
                if (res.status === 200) {
                    console.log('Success')
                    alert('Change Applied');
                    navigate('/');
                } else if (res.status === 404) {
                    console.log('Cant Find data');
                    alert('Cant Find data');
                    navigate('/');
                } else {
                    console.log("Status: " + res.status)
                    return Promise.reject("server")
                }
            })
            .finally(() => setTimeout(() => { setLoadingState(false) }, 2500))
            .catch(err => {
                console.log(err);
                alert('Failed to edit...');
            })
    }

    return (
        <div style={{ overflowX: 'hidden' }} className='mt-2'>
            <div className='d-flex justify-content-center'>
                <div className='row container-fluid justify-content-around my-2'>
                    <div className='col-md-6'>
                        <CarouselCard urlArrayId={urlArrayId} />
                    </div>

                    <form className='col' onSubmit={e => Submit(e)} ref={formRef}>
                        <h1 className='h1 text-center mt-2'>Edit <span className=' h3 text-muted'> {kode} </span></h1>

                        <div className='row'>
                            <div className='col-sm-8'>
                                <label className='mb-1' htmlFor='kodeInp'>Kode</label>
                                <input className="container-fluid border rounded p-2 mb-1 bg-dark bg-opacity-25 focusNone text-white" id='kodeInp' type="text" readOnly="readonly" value={kode} name="kode" aria-label="Edit" />
                            </div>
                            <div className='col-sm-4 d-flex align-items-center'>
                                <input className="form-check-input" type="checkbox" name='favorit' id="flexCheckChecked" value="true" 
                                onChange={() => setFavorit(!favorit)} checked={favorit} aria-label="Edit" />
                                <label className="form-check-label m-1" htmlFor="flexCheckChecked">
                                    Favorit
                                </label>
                            </div>
                        </div>

                        <div className='row'>
                            <div className='col'>
                                <label className='mb-1' htmlFor='warnaInp'>Warna</label>
                                <input className="form-control" id='warnaInp' type="text" placeholder={data.warna} name="warna" aria-label="Edit" />
                            </div>

                            <div className='col'>
                                <label className='mb-1' htmlFor='hargaInp'>Harga</label>
                                <div className='input-group'>
                                    <span className="input-group-text bg-dark bg-opacity-25 text-white" id="hargaInp">Rp.</span>
                                    <input className="form-control" id='hargaInp' type="number" placeholder={data.harga} name="harga" aria-label="Edit"
                                        min={0} onWheel={(e) => e.target.blur()} />
                                </div>
                            </div>
                        </div>
                        <hr />

                        <div className='row row-col-2'>
                            {urlArrayId.map((el, index) => {
                                return (
                                    <div key={'div' + index} className='mb-2 col-12 col-sm-6 col-md-12 col-lg-6'>
                                        <label key={'label' + index} className='mb-1' htmlFor='urlArray'>{index === 0 ? 'Ubah Foto Utama' : `Ubah Foto ke-${index + 1}`}</label>
                                        <a className='ms-3' target="_blank" rel="noreferrer"
                                            href={el}
                                            id='urlArray'>Link Foto
                                        </a>
                                        <input className="form-control " id='tambahFoto' type="file" name={"change_" + index} aria-label="Edit" accept='.jpg, .jpeg'/>
                                    </ div>
                                )
                            })}
                        </div>

                        <hr />
                        <label className='mb-1' htmlFor='tambahFoto'>Tambah Foto</label>
                        <input className="form-control mb-4" id='tambahFoto' type="file" name="add" aria-label="Edit" multiple accept='.jpg, .jpeg'/>

                        <div className='d-flex justify-content-between mb-4'>
                            <button className='btn btn-primary' disabled={loadingState ? true : false}>{loadingState ? 'Loading...' : 'Submit'}</button>
                            <button className="btn btn-danger" type='button' disabled={loadingState ? true : false}
                                onClick={() => {
                                    console.log(deleteConfirm)
                                    if (deleteConfirm) {
                                        Delete()
                                    } else setDeleteConfirm(true)
                                }}>
                                {!deleteConfirm ? 'Delete Gown Data' : 'Are You Sure?'}
                            </button>
                        </div>

                    </form>
                </div>
            </div>
        </div>

    )
}